/*COLORS:*/
@keyframes moveLeft {
  /*animation start*/
  0% {
    opacity: 0;
    transform: translateX(-10rem); }
  /*animation before ends*/
  80% {
    transform: translateX(1rem); }
  /*animation end*/
  100% {
    opacity: 1;
    transform: translateX(0rem); } }

@keyframes moveRight {
  /*animation start*/
  0% {
    opacity: 0;
    transform: translateX(10rem); }
  /*animation before ends*/
  80% {
    transform: translateX(-1rem); }
  /*animation end*/
  100% {
    opacity: 1;
    transform: translateX(0rem); } }

@keyframes moveUp {
  /*animation start*/
  0% {
    opacity: 0;
    transform: translateY(1rem); }
  /*animation before ends*/
  80% {
    transform: translateY(-1rem); }
  /*animation end*/
  100% {
    opacity: 1;
    transform: translateY(0rem); } }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  /*box-sizig prop defined in body el is applied to all els*/ }

html {
  /* in here we define the root font-size so that we can use "2em"(2*value from the parent) 
  and "2rem"(2*value from the root) */
  /* if we wanna change all document font-sizes we just change root font-size (by default is 16px) */
  /* font-size: 10px; */
  /* now 10px===1rem and we change px into rems*/
  /* IT IS NOT A GOOD PRACTICE defining root font-size with px , cuz when users zoom in/out it wont be changed
  we define in percentage those 10px ((10/16)*100) */
  font-size: 62.5%; }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 56.25%; } }
  @media only screen and (max-width: 56.25em) {
    html {
      font-size: 50%; } }
  @media only screen and (min-width: 112.5em) {
    html {
      font-size: 75%; } }

