.btn {
  /*active is when click the button*/
  /*creating another child btn hidden behind*/ }
  .btn, .btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    display: inline-block;
    border-radius: 10rem;
    position: relative;
    /*which props we wanna animate(hover and active),animation time*/
    transition: all 0.2s;
    border: none; }
  .btn--animated {
    /*import created animation*/
    animation-name: moveUp;
    animation-duration: 1s; }
  .btn:hover {
    transform: translateY(-0.3rem);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
    /*offset,y-down,blur,color*/
    /*activate the "after state" when hover btn*/ }
    .btn:hover::after {
      transform: scale(1.4);
      /*scale the state(double the size)*/
      opacity: 0; }
  .btn:active, .btn:focus {
    outline: none;
    transform: translateY(-0.1rem);
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2); }
  .btn--white {
    background-color: #fff;
    color: #777;
    padding: 1.5rem 4rem; }
    .btn--white::after {
      background-color: #fff; }
  .btn--green {
    background-color: #7ed56f;
    color: #fff;
    padding: 1.5rem 4rem; }
    .btn--green::after {
      background-color: #7ed56f; }
  .btn::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 10rem;
    position: absolute;
    /*must add relative to the parent btn*/
    top: 0;
    left: 0;
    z-index: -1;
    transition: all 0.4s; }

.btn-text:link, .btn-text:visited {
